                    <v-icon left>mdi-flag</v-icon>
                    Flag
                  </v-btn>
                </v-card-actions>
              </v-col>
            </v-row>

            <v-item-group v-if="EmbryoStageModule" selected-class="bg-primary">
              <v-container>
                <v-row>
                  <v-col
                    v-for="n in EmbryoStageOptions"
