                    cols="4"
                    md="4"
                    xs="4"
                    sm="4"
                    lg="4"
                  >
                    <v-item>
                      <v-card
                        :class="[
                          'd-flex align-center',
                          { primary: cardClicked === n }
                        ]"
                        dark
                        @click="submit(n)"
                        @touchstart="startTouch(n)"
                        @touchend="endTouch"
                      >
                        <div :class="textClass" class="flex-grow-1 text-center">
                          {{ n }}
                        </div>
                      </v-card>
                    </v-item>
                  </v-col>
                </v-row>
              </v-container>
            </v-item-group>

            <v-divider v-if="EmbryoStageModule"></v-divider>

            <!-- ICM Grade Selection -->
            <v-item-group v-if="ICMModule" selected-class="bg-primary">
              <v-container>
                <v-row>
                  <v-col
                    v-for="n in ICMGradeOptions"
                    :key="n"
                    cols="6"
                    md="6"
                    xs="6"
                    sm="6"
                    lg="6"
                  >
                    <v-item>
                      <v-card
                        :class="[
                          'd-flex align-center',
                          { primary: cardClicked === n }
                        ]"
                        dark
                        @click="submit(n)"
                        @touchstart="startTouch(n)"
                        @touchend="endTouch"
                      >
                        <div :class="textClass" class="flex-grow-1 text-center">
                          {{ n }}
                        </div>
                      </v-card>
                    </v-item>
                  </v-col>
                </v-row>
              </v-container>
            </v-item-group>

            <!-- Trophectoderm Grade Selection -->

            <v-item-group
              v-if="TrophectodermModule"
              selected-class="bg-primary"
            >
              <v-container>
                <v-row>
                  <v-col
                    v-for="n in TrophectodermGradeOptions"
